import React from 'react'
import './ProductPreview.css'
import { JumpTo } from '../utils/url'

class ProductPreview extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
    };
  } 

  render() {
    let img_path = process.env.PUBLIC_URL + this.props.product_detail.product_img_path[0]
    img_path = window.innerWidth <= 600 ? img_path.replace('.jpg', '_icon.jpg') : img_path.replace('.jpg', '_low.jpg')

    let product_img =
      <img
        src={ img_path }
        alt=""
        className='ProductPreview_image'
        onError={(e)=>{e.target.onerror = null; e.target.src=process.env.PUBLIC_URL + "/default_error.jpg"}}
      />
    
    let jump_url = `/productdetail/` + this.props.product_detail._id

    return (
      <div className="ProductPreview" onClick={  () => { JumpTo(jump_url) } }>
        <p className='ProductPreview_line'>{ this.props.product_detail.product_name }</p>

        { product_img }

        <p className='ProductPreview_line'>
            { this.props.product_detail.product_price } ÂÖÉ
        </p>
      </div>
    );
  }
}

export default ProductPreview